<template>

</template>

<script>
    export default {
        name: 'csChartRadar',
        data() {
            return {
                polygon: 3,   //
                canvasHeight: 300,  //canvas
                canvasWidth: 300,   //canvas
                canvasRadius: 100,   //canvas
                xCenter: 0,   //X
                lineWidth: 1,   //
                yCenter: 0,   //Y
                isData: true,    //
                dataArr: [],  //
                outerColor: '#E5E5E5',   //
                isInside: true,   //
                insideColor: '#EaEaEa',   //
                insideNumber: 6,   //
                isCable: true,   //
                isDataCable: true,   //
                isDataPoint: true,   //
                dataPointColor: '#00B9EF',   //
                dataCableColor: '#00B9EF',   //
                cableColor: '#E8E8E8',   //
                fillColor: 'rgba(0, 185, 239, 0.14)',   //
                fontColor: '#999',   //
                fontSize: 11,   //
                isSaveImage: true,   //
                imageType: 'png'   //
            }
        },
        mounted() {
            this.startDraw()
        },
        methods: {
            /**
             
             @param
             @return
             */
            startDraw() {
                try {
                    //
                    const canvas = document.createElement('canvas')
                    canvas.width = this.canvasWidth
                    canvas.height = this.canvasHeight
                    let ctx = canvas.getContext('2d')
                    let lineArr = []
                    let rAngle = Math.PI * 2 / this.polygon; // 
                    let xCenter = this.xCenter ? this.xCenter : this.canvasWidth / 2, yCenter = this.yCenter ? this.yCenter : this.canvasHeight / 2 // 
                    let curR = this.canvasRadius;   // 
                    ctx.strokeStyle = this.outerColor  // 
                    ctx.lineWidth = this.lineWidth
                    ctx.beginPath()   //
                    for (let i = 0; i < this.polygon; i++) {
                        lineArr[i] = {}
                        lineArr[i].y = yCenter - curR * Math.cos(rAngle * i)
                        lineArr[i].x = xCenter - curR * Math.sin(rAngle * i)
                        ctx.lineTo(lineArr[i].x, lineArr[i].y)
                    }
                    ctx.closePath()   //
                    ctx.stroke()    //
                    ctx.save()
                    ctx.restore()    // 

                    //
                    if(this.isInside) {
                        for(let j = 1; j < this.insideNumber + 1; j++) {
                            ctx.strokeStyle = this.insideColor  // 
                            ctx.lineWidth = this.lineWidth
                            ctx.beginPath();   //
                            var curRadius = this.canvasRadius * (this.insideNumber - j) / this.insideNumber   // 
                            for (let i = 0; i < this.polygon; i++) {
                                const yInside = yCenter - curRadius * Math.cos(rAngle * i)
                                const xInside = xCenter - curRadius * Math.sin(rAngle * i)
                                ctx.setLineDash([2])
                                ctx.lineTo(xInside, yInside)
                            }
                            ctx.closePath()   //
                            ctx.stroke()    //
                            ctx.setLineDash([])
                            ctx.save()
                            ctx.restore()
                        }
                    }

                    //
                    if(this.isCable) {
                        ctx.strokeStyle = this.cableColor
                        ctx.lineWidth = this.lineWidth / 2
                        ctx.beginPath()
                        for (let k = 0; k < this.polygon; k++) {
                            ctx.moveTo(lineArr[k].x, lineArr[k].y)
                            ctx.lineTo(xCenter, yCenter)
                            ctx.stroke()
                        }
                        ctx.closePath()
                        ctx.save()
                        ctx.restore()
                    }


                    // 
                    if(this.isData) {
                        ctx.beginPath()
                        for (let i = 0; i < this.polygon; i++) {
                            lineArr[i].yEnd = yCenter - curR * Math.cos(rAngle * i) * this.dataArr[i].value
                            lineArr[i].xEnd = xCenter - curR * Math.sin(rAngle * i) * this.dataArr[i].value
                            let fillY = yCenter - curR * Math.cos(rAngle * i) * this.dataArr[i].value * 0.95
                            let fillX = xCenter - curR * Math.sin(rAngle * i) * this.dataArr[i].value * 0.95
                            ctx.lineTo(fillX, fillY)
                        }
                        ctx.fillStyle = this.fillColor
                        ctx.fill()   //
                        ctx.stroke()
                        ctx.save()
                        ctx.closePath()
                        ctx.restore()
                    }

                    //
                    if(this.isDataCable) {
                        ctx.beginPath()
                        ctx.strokeStyle = this.dataCableColor
                        ctx.lineWidth = this.lineWidth
                        for (let i = 0; i < this.polygon; i++) {
                            ctx.lineTo(lineArr[i].xEnd, lineArr[i].yEnd)
                        }
                        ctx.closePath()
                        ctx.stroke()    //
                        ctx.save()
                        ctx.restore()    // 
                    }

                    //
                    if(this.isDataPoint) {
                        ctx.fillStyle = this.dataPointColor
                        for (let i = 0; i < this.polygon; i++) {
                            ctx.beginPath()
                            ctx.arc(lineArr[i].xEnd, lineArr[i].yEnd, 4, 0, Math.PI*2, true);
                            ctx.fill()
                            ctx.closePath()
                        }
                        ctx.save()
                        ctx.restore()
                    }

                    //
                    if(this.isData) {
                        ctx.font = this.fontSize + 'px Microsoft Yahei'  //
                        ctx.fillStyle = this.fontColor  // 
                        for (let i = 0; i < this.polygon; i++) {
                            let s_width = ctx.measureText(this.dataArr[i].text).width //
                            const xText = xCenter - s_width / 2 + (-curR - s_width / 2 - 16) * Math.sin(rAngle * i)
                            const yText = yCenter + this.fontSize / 2 + (-curR - this.fontSize / 2 - 8) * Math.cos(rAngle * i)
                            ctx.fillText(this.dataArr[i].text, xText, yText)
                        }
                        ctx.save()
                        ctx.restore()
                    }
                    if(this.isSaveImage) {
                        this.finish(this.convertCanvasToImage(canvas))
                    } else {
                        this.finish(canvas)
                    }
                }
                catch(res) {
                    this.fail(res)
                }
            },
            /**
             canvas
             @param {canvas} canvas  canvas
             @return
             */
            convertCanvasToImage(canvas) {
                let image = new Image()
                image.src = canvas.toDataURL(`image/${this.imageType}`)
                return image
            }
        },
    }
</script>




