import '../main'
import csLazy from './src/lazy.vue'

csLazy.install = function (Vue) {
    Vue.directive('lazy', {    //
        inserted: function (el, binding, vnode ) {
            if(typeof IntersectionObserver !== 'undefined') {   //IntersectionObserver
                var observer  = new IntersectionObserver(
                    function (entries) {
                        entries.map((item)=> {
                            if(item.isIntersecting) {
                                if(vnode.tag === 'img') {   //
                                    el.src = binding.value
                                }
                                observer.unobserve(el)
                            } else {
                                if(vnode.tag === 'img') {   //
                                    el.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'
                                }
                            }
                        })
                    }
                )
                observer.observe(el)
            }
        }
    })

    Vue.directive('lazyBGImg', {    //
        inserted: function (el, binding, vnode ) {
            if(typeof IntersectionObserver !== 'undefined') {   //IntersectionObserver
                var observer  = new IntersectionObserver(
                    function (entries) {
                        entries.map((item)=> {
                            if(item.isIntersecting) {
                                el.style.backgroundImage = `url(${binding.value})`
                                observer.unobserve(el)
                            } else {
                                el.style.backgroundImage = 'url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7)'
                            }
                        })
                    }
                )
                observer.observe(el)
            }
        }
    })
}

export default csLazy


